<script setup>
import { ref, watch, onMounted, onBeforeUnmount, nextTick } from "vue";
import axios from "axios";
import Charts from "@/components/Charts.vue";
import RouterLink from "@/components/RouterLink.vue" 
import { API_BASE_URL } from "@/config/config";
import BigTable from "@/components/BigTable.vue";
const bigTableRef = ref(null);

import { useUserStore } from "@/stores/user.js";
const userData = useUserStore();

//chart
const settings1 = ref({
  title: "Daily Trend",
  type: "bar",
  api: {
    url: API_BASE_URL + "/chart/de_running_total",
    params: [14, "", ""], //days, customer, location
  },
  columns: {
    label: ["date"],
    datas: ["running_total", "daily_down", "daily_done"],
  },
  colors: ["red", "orange", "green"],
  labels: ["Total Down", "Units Down / Day", "Units Done / Day"],
});

const settings11 = ref({
  title: "Daily Trend",
  type: "line",
  api: {
    url: API_BASE_URL + "/chart/de_running_total",
    params: [14, "", ""], //days, customer, location
  },
  columns: {
    label: ["date"],
    datas: ["running_total", "daily_down", "daily_done"],
  },
  colors: ["red", "orange", "green"],
  labels: ["Total Down", "Units Down / Day", "Units Done / Day"],
});

const settings2 = ref({
  title: "Unit Summary",
  type: "pie",
  api: {
    url: API_BASE_URL + "/chart/de_total_rental",
    params: ["", ""]
  },
  columns: {
    datas: ["unit_rental", "spare_exists_false", "spare_exists_true"],
  },
  colors: ["green", "red", "orange"],
  labels: ["Unit Rental", "PA Unit", "PA Rental"],
});

const settings3 = ref({
  title: "Customer Summary",
  type: "bar",
  api: {
    url: API_BASE_URL + "/chart/de_customer_summary",
  },
  columns: {
    label: ["customer"],
    datas: ["total_unit", "total_break"],
  },
  colors: ["green", "red"],
  labels: ["Total Unit", "Total Break"],
});

const settings4 = ref({
  title: "Location Summary",
  type: "bar",
  api: {
    url: API_BASE_URL + "/chart/de_location_summary"
  },
  columns: {
    label: ["alt_location"],
    datas: ["total_unit", "total_break"],
  },
  colors: ["green", "red"],
  labels: ["Total Unit", "Total Break"],
});

// Tabs
const currentTab = ref("");
const tabs = [
  { label: "All", value: "" },
  { label: "Site Summary", value: "site-summary" },
  { label: "Daily Trend", value: "0" },
  { label: "Unit Summary", value: "1" },
  { label: "Customer Summary", value: "2" },
  { label: "Location Summary", value: "3" },
];

// Table config
const settings = ref({ title: "" });

const formData = ref({});

const fields = ref({
  firstCol: {label: "TK NO", field: "tk_no", type: "type"},
  merge: [
    { fields: ["hour_in", "hour_out"], to: "hour" },
    { fields: ["jarak_in", "jarak_out"], to: "jarak" }
  ],
  priority: ["customer", "alt_location"],
  unpriority: ["username", "createdAt", "lastUpdated"],
  hidden: ["id", "done"],
  toRemove: ["vehicle_type", "plate_no"],
  uneditable: ["id", "plan_hub"],
  checkbox: ["spare_exists"],
  date: [],
  datetime: ["down_since", "estimated_return"]
});

const urls = ref({
  data: `${userData.apiBaseUrl}/de_site`,
  dropdowns: {
    vehicle_type: {
      url: userData.apiBaseUrl + "/dropdowns/vehicle_type",
      valueField: "vehicle_type",
      column: "vehicle_type"
    },
    tk_no: {
      url: userData.apiBaseUrl + "/dropdowns/tk_no",
      valueField: "tk_no",
      column: "tk_no"
    },
  },
  get: {
    vehicle_type: {
      url: userData.apiBaseUrl + "/get/vehicle_type",
      valueField: "vehicle_type",
      column: "vehicle_type"
    }
  }
});

// Status Count & Waktu
const workingCount = ref([0,0]);
const smDownCount = ref([0,0]);
const umDownCount = ref([0,0]);
const currentTime = ref('');

// Update jam
function updateClock() {
  const now = new Date();
  const hours = now.getHours().toString().padStart(2, "0");
  const minutes = now.getMinutes().toString().padStart(2, "0");
  const seconds = now.getSeconds().toString().padStart(2, "0");
  currentTime.value = `${hours}:${minutes}:${seconds}`;
}


const fetchTotal = async () => {
  try {
    const response = await axios.get(API_BASE_URL + "/chart/de_total_rental", {
        params: {
            params: ["", ""]
        },
    });
    console.log("response : ", response.data);
    const _total = response.data[0].unit_rental + response.data[0].spare_exists_false + response.data[0].spare_exists_true;
    workingCount.value[0] = response.data[0].unit_rental;
    smDownCount.value[0] = response.data[0].spare_exists_false + response.data[0].spare_exists_true;
    umDownCount.value[0] = response.data[0].spare_exists_false;
    workingCount.value[1] = (workingCount.value[0] / _total *  100).toFixed(2);
    smDownCount.value[1] = (smDownCount.value[0] / _total * 100).toFixed(2);
    umDownCount.value[1] = (umDownCount.value[0] / _total * 100).toFixed(2);
    console.log(smDownCount.value);
  } catch (error) {
      console.error("Fetch error:", error);
  }
};
// Lifecycle
onMounted(async () => {
  // Inisialisasi form kosong
  await fetchTotal();
  
  // Mulai jam berjalan
  updateClock();
  setInterval(updateClock, 1000);
});

onBeforeUnmount(() => {
});
</script>


<template>
  <div class="content" :style="{ paddingTop: showHeader ? '100px' : '0px' }">
    <!-- Header -->
    <div class="mb-2">
      <h1 class="h4 fw-bold">Down Equipment Dashboard</h1>
      <div class="text-end">
        <h5 class="fw-bold text-primary"></h5>
        <div>{{ currentTime }}</div>
      </div>
      <!-- Status Cards -->
     <div class="row text-center mb-4">
      <div class="col">
        <div class="card border-success">
          <div class="card-body">
            <h3 class="text-success">{{ workingCount[0] }}</h3>
            <p class="fw-bold mb-0">On Hire (UIC)</p>
          </div>
        </div>
      </div>
      <div class="col">
        <div class="card border-danger">
          <div class="card-body">
            <h3 class="text-danger"style="text-align: center;">
            <span style="margin-right: 10px;">{{ smDownCount[1] }}%</span>
            ||
            <span style="margin-left: 10px;">{{ smDownCount[0] }}</span>
          </h3>
            <p class="fw-bold mb-0">PA Unit</p>
          </div>
        </div>
      </div>
      <div class="col">
        <div class="card border-warning">
          <div class="card-body">
            <h3 class="text-warning" style="text-align: center;">
            <span style="margin-right: 10px;">{{ umDownCount[1] }}%</span>
            ||
            <span style="margin-left: 10px;">{{ umDownCount[0] }}</span>
            </h3>
            <p class="fw-bold mb-0">PA Rental</p>
          </div>
        </div>
      </div>
    </div>
    </div>
     

    <!-- Filter dan Tabs 
    <div class="mb-4">
      <div class="d-flex flex-wrap align-items-center gap-3 mb-3">
        <label>Select Range:</label>
        <input type="date" v-model="startDate" class="form-control form-control-sm w-auto" />
        <span>to</span>
        <input type="date" v-model="endDate" class="form-control form-control-sm w-auto" />
        <button @click="applyFilter" class="btn btn-sm btn-danger">Apply</button>
      </div>
      -->
      

      <!-- Tab Navigasi -->
      <ul class="nav nav-tabs">
        <li class="nav-item" v-for="tab in tabs" :key="tab.value">
          <button
            class="nav-link"
            :class="{ active: currentTab === tab.value }"
            @click="currentTab = tab.value"
          >
            {{ tab.label }}
          </button>
        </li>
      </ul>
    

    <!-- Site Summary -->
    <div v-if="currentTab === '' || currentTab === 'site-summary'" class="card mb-4">
      <div class="card-body">
        <h5 class="card-title text-danger">Site Summary</h5>
        <BigTable
          ref="bigTableRef"
          :settings="settings"
          :urls="urls"
          :fields="fields"
          :canInsert="false"
          :canUpdate="false"
          ></BigTable>
      </div>
    </div>

    <!-- Charts Section -->
    <div v-if="currentTab === '' || ['0', '1', '2', '3'].includes(currentTab)" class="row g-4 mb-4">

       <!-- Unit Down by Site -->
  <div v-if="currentTab === '' || currentTab === '0'" class="col-md-12">
        <div class="card h-100"> 
            <Charts :settings=settings1></Charts>
        </div>
      </div>
  <div v-if="currentTab === '' || currentTab === '0'" class="col-md-12">
        <div class="card h-100"> 
            <Charts :settings=settings11></Charts>
        </div>
  </div>

      <!-- Unit Summary -->
<div v-if="currentTab === '' || currentTab === '1'" class="col-md-12">
  <div class="card h-100">
      <Charts :settings=settings2></Charts>
  </div>
</div>

    
      <!-- Customer Summary -->
      <div v-if="currentTab === '' || currentTab === '2'" class="col-md-12">
        <div class="card h-100">
            <Charts :settings=settings3></Charts>
        </div>
      </div>

    <!-- Location Summary -->
    <div v-if="currentTab === '' || currentTab === '3'" class="col-md-12">
        <div class="card h-100">
            <Charts :settings=settings4></Charts>
      </div>
    </div>
  </div>


    <!-- Back Button 
    <div>
      <RouterLink to="/de/down-equipment" color="danger" size="md">Back to Down Equipment</RouterLink>
    </div>-->
  </div>
</template>




<style scoped>
.border-success {
  border: 2px solid #198754 !important; /* Bootstrap green */
}
.border-danger {
  border: 2px solid #dc3545 !important; /* Bootstrap red */
}
.border-warning {
  border: 2px solid #ffac07 !important; /* Bootstrap yellow */
}
.card h3 {
  font-size: 28px;
}

canvas {
  width: 100% !important;
  max-height: 300px;
}
.nav-tabs .nav-link.active {
  background-color: #dc3545;
  color: #fff;
}
.nav-tabs .nav-link {
  color: #000; /* Warna hitam untuk text tab tidak aktif */
}
.card {
  width: 100%;
}
</style>




